import { Navigate, useNavigate } from "react-router-dom";
import React, { ChangeEvent, FormEvent, useState } from "react";
import classNames from "classnames";
import {
  Button,
  Input,
} from "@ya.praktikum/react-developer-burger-ui-components";

import InputPassword from "../input-password/InputPassword";
import { useAuth } from "../auth";
import Loading from "../../components/loading/Loading";
import { resetPassword } from "../../utils/api";

import css from "../index.module.scss";

const ResetPassword = () => {
  const navigate = useNavigate();
  const {
    isAuthenticated,
    isLoading,
    isActivePasswordReset,
    setIsActivePasswordReset,
  } = useAuth();

  const [value, setValue] = useState({
    password: "",
    token: "",
  });

  const [hasError, setHasError] = useState();

  const changeValue = (
    event: ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    setValue(() => ({
      ...value,
      [event.target.name]: event.target.value,
    }));
  };

  const post = async (event: FormEvent) => {
    event.preventDefault();
    await resetPassword(value)
      .then((res) => {
        return res.json();
      })
      .then((response) => {
        if (response.success) {
          navigate("/login");
          setIsActivePasswordReset(false);
        }

        if (!response.success) {
          setHasError(response.message);
        }
      })
      .catch(() => {
        console.log("error");
      });
  };

  if (!isActivePasswordReset) {
    return <Navigate to="/forgot-password" replace />;
  }

  if (isAuthenticated) {
    return <Navigate to="/" replace />;
  }

  if (isLoading) {
    return <Loading style={{ height: "70vh" }} />;
  }

  return (
    <section className={css.form}>
      <form className={css.form_container} onSubmit={post}>
        <h1 className="text text_type_main-large"> </h1>
        <InputPassword
          changeValue={changeValue}
          value={value}
          placeholder={"  "}
        />
        <Input
          type={"text"}
          placeholder={"   "}
          onChange={changeValue}
          name={"token"}
          size={"default"}
          extraClass="ml-1"
          value={value.token}
        />
        {hasError && (
          <p
            className={classNames(
              "text text_type_main-default",
              css.error_message
            )}
          >
            {hasError}
          </p>
        )}
        <Button htmlType="submit" type="primary" size="medium">
          
        </Button>
      </form>
      <div className={css.form_text}>
        <p className="text text_type_main-default text_color_inactive">
           ?{" "}
          <a
            className={classNames("text text_type_main-default", css.form_link)}
            href="/login"
          >
            
          </a>
        </p>
      </div>
    </section>
  );
};

export default ResetPassword;
